"use client";
import { motion, useScroll, useTransform } from "framer-motion";
import { useRef } from "react";
import MagneticButton from "@/components/ui/MagneticButton";
import HeroDevice from "@/components/ui/HeroDevice";
import DownloadBadges from "@/components/ui/DownloadBadges";

const containerVariants = {
  hidden: { opacity: 0 },
  visible: {
    opacity: 1,
    transition: {
      staggerChildren: 0.1,
      delayChildren: 0.2,
    },
  },
};

const itemVariants = {
  hidden: { opacity: 0, y: 20 },
  visible: {
    opacity: 1,
    y: 0,
    transition: {
      duration: 0.8,
    },
  },
};

const splitVariants = {
  hidden: { opacity: 0, y: 40 },
  visible: {
    opacity: 1,
    y: 0,
    transition: {
      duration: 0.8,
    },
  },
};

export default function HeroReveal() {
  const ref = useRef<HTMLDivElement>(null);
  const { scrollYProgress } = useScroll({ target: ref, offset: ["start start", "end center"] });

  // Scroll parallax effects - secondary to entrance animations
  const parallaxY = useTransform(scrollYProgress, [0, 1], [0, -100]);
  const parallaxScale = useTransform(scrollYProgress, [0, 1], [1, 1.05]);
  const parallaxOpacity = useTransform(scrollYProgress, [0, 0.6, 1], [1, 1, 0.8]);

  return (
    <section ref={ref} className="relative pt-24 bg-emerald-soft">
      <div className="min-h-screen flex items-center justify-center overflow-hidden">
        <div className="container-max w-full">
          <motion.div
            className="max-w-5xl mx-auto"
            variants={containerVariants}
            initial="hidden"
            animate="visible"
          >
            {/* Main Title with Parallax */}
            <motion.div
              style={{ y: parallaxY, scale: parallaxScale, opacity: parallaxOpacity }}
              className="will-change-transform"
            >
              <motion.h1
                variants={splitVariants}
                className="font-heading text-6xl md:text-7xl lg:text-8xl text-emerald leading-tight font-bold"
              >
                <motion.span variants={splitVariants} className="block">
                  The Heart
                </motion.span>
                <motion.span variants={splitVariants} className="block">
                  of Peace
                </motion.span>
              </motion.h1>
            </motion.div>

            {/* Subtitle */}
            <motion.h2
              variants={itemVariants}
              className="mt-8 font-heading text-3xl md:text-4xl text-emerald/90 font-semibold"
            >
              Your Faith, Simplified
            </motion.h2>

            {/* Description */}
            <motion.p
              variants={itemVariants}
              className="mt-6 text-emerald/75 max-w-2xl text-lg leading-relaxed"
            >
              Transform your phone into the perfect Islamic companion. QalbyMuslim brings you accurate prayer times, daily Quranic inspiration, and spiritual guidance in a beautifully designed app.
            </motion.p>

            {/* CTA Button */}
            <motion.div
              variants={itemVariants}
              className="mt-10"
            >
              <MagneticButton label="Download Qalby Muslim" href="#download" />
            </motion.div>

            {/* Device */}
            <motion.div
              variants={itemVariants}
              className="mt-16 flex justify-center md:justify-start"
            >
              <HeroDevice className="mt-0" />
            </motion.div>

            {/* Download Badges */}
            <motion.div
              variants={itemVariants}
              className="mt-8 flex justify-center md:justify-start pb-12"
            >
              <DownloadBadges />
            </motion.div>
          </motion.div>
        </div>
      </div>
    </section>
  );
}
